.course-card {
  padding: 1rem;
  border-radius: 1rem;
  flex: 25%;
  max-width: calc(25% - 0.75rem);
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}
.cc-top img {
  width: 100%;
  height: 12rem;
  object-fit: cover;
  border-radius: 0.5rem;
}
.cc-bottom {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}
.cc-info {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
